<template>
    <div :id="randomId" :style="{width: paramWidth, height: paramHeight}">

    </div>
</template>

<script>

    export default {
        name: "chart-com",
        props: {
            //
            paramWidth: '',
            paramHeight: '',
            echartOption:null,
            resizeChart: Boolean
        },
        data: function () {
            return {
                chart:null
                // usualScheme:{//
                //     barOption:{
                //         legend: {},//
                //         tooltip: {},//legend
                //         dataset: { //
                //             dimensions: ['year', '', '', ''],
                //             source: [
                //                 {year: '2015', '': 43.3, '': 85.8, '': 93.7},
                //                 {year: '2016', '': 83.1, '': 73.4, '': 55.1},
                //                 {year: '2017', '': 86.4, '': 65.2, '': 82.5},
                //                 {year: '2018', '': 72.4, '': 53.9, '': 39.1}
                //             ]
                //         },
                //         //
                //         series: [
                //             {type: 'bar'},//
                //             {type: 'bar'},
                //             {type: 'bar'}
                //         ],
                //
                //
                //         //
                //         xAxis: {type: 'category'},//x
                //         yAxis: {},//y
                //     }
                // },
                //
                // specialScheme:{//
                //
                // }
            }
        },
        computed:{
            randomId:function(){
                return 'chart-com' + Math.random().toString();
            }
        },
        methods: {
            draw: function (echartOption) {

                //!ID
                let chartDom = document.getElementById(this.randomId.toString());
                let chart = this.$echarts.init(chartDom, 'Cat');
                chart.clear();
                chart.setOption(echartOption);
                this.chart = chart;
            },
            resize:function(){
                // console.log('resize')
                this.chart.resize();
            }
        },
        watch:{
            echartOption:function(newObj, oldObj){

                this.draw(newObj);
            },
            resizeChart:function(newValue, oldValue){
                this.resize();
            }

        },
        mounted:function(){
            // console.log(this.echartOption);
            if(this.echartOption !== null){
                this.draw(this.echartOption);
            }
        }

    }
</script>

